import url from '../../common/url.js';
import { mapMutations } from 'vuex';
export default {
	onLoad() {
		_self = this;
	},
	data() {
		return {
			userName: '',
			password: '',
			takeInfo: {}
		};
	},
	methods: {
		...mapMutations(['login']),
		bindLogin() {
			if (_self.userName.length == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入用户名'
				});
				return;
			}
			if (_self.password.length == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的密码'
				});
				return;
			}
			uni.request({
				url: url.businessSite + 'login',
				data: {
					userName: _self.userName,
					password: _self.password
				},
				dataType: 'json',
				method: 'POST',
				success: res => {
					console.log(JSON.stringify(res.data));
					if (res.data.code == '-1') {
						uni.showToast({
							title: '用户不存在！请重新输入',
							icon: 'none',
							duration: 2000
						});
					} else {
						uni.request({
							url: url.businessSite + 'get_info',
							data: {
								Token: res.data.token
							},
							method: 'GET',
							dataType: 'json',
							success: response => {
								console.log(JSON.stringify(response.data));
								_self.takeInfo = response.data;
								var name = _self.takeInfo.name;
								var user_id = _self.takeInfo.user_id;
								var displayname = _self.takeInfo.displayname;
								// 全局同步缓存
								try {
									uni.setStorage(
										{
											key: 'nameStorage',
											data: name
										},
										{
											key: 'idStorage',
											data: user_id
										},
										{
											key: 'displaynameStorage',
											data: displayname
										}
									);
								} catch (e) {
									console.log(e);
								}
							}
						});
						uni.navigateTo({
							url: '../index/index'
						});
					}
				}
			});
		}
	}
};
